.stickybanner__container {
  position: fixed;
  z-index: 997;
  width: 100%;
}

.stickybanner__plus {
  vertical-align: middle;
  // display: inline-block;
  // position: relative;
  width: 1.5rem;
  height: 1.5rem;
  margin: auto;
  border-radius: 25px;
  text-align: center;
  font-size: 1.25rem;
  -webkit-text-stroke: 2px;
  cursor: pointer;
  -webkit-text-stroke-color: #8f7247;
}
.stickybanner__ribbon_content_left,
.stickybanner__ribbon_content_right {
  width: 25%;
}
.stickybanner__ribbon_content_text {
  width: 50%;
  margin: auto;
  padding: 0.5rem 1rem 0;
}
.stickybanner__arrow_icon_opened {
  transform: translate(-180deg);
  -ms-transform: rotate(-180deg);
  -webkit-transform: rotate(-180deg);
}
.stickybanner__ribbon {
  cursor: pointer;
  position: relative;
  .stickybanner__plus {
    position: absolute;
    left: 50%;
    display: block;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.33);
    transition: margin-top 0.4s, transform 0.4s;
    transform: translateX(-50%) rotate(45deg);
    padding-top: 0.625rem;
    width: 2.5rem;
    height: 2.5rem;
    background-color: #8f7247;
    margin-top: -23px;

    i {
      position: absolute;
      top: calc(50% + 1px);
      left: calc(50% - 8px);
    }

    &.overflow {
      transform: translateX(-50%) rotate(0deg);
    }
  }
}
.stickybanner__ribbon_optout {
  position: absolute;
  right: 0;
  padding: 0.375rem;
  cursor: pointer;
}
.stickybanner__content {
  background-color: #fff;
  color: #000;
  padding: 0 0.75rem 0.75rem;
  width: 100%;
  display: none;
}
.stickybanner__container {
  box-shadow: 0 -2px 3px 0 rgba(0, 0, 0, 0.04);
  border: 1px solid #e6e6e6;
  background-color: #fff;
}
