.stickybanner__ribbon_content {
  text-align: center;
  width: 100%;
  max-width: 996px;
  min-height: 80px;
  margin: auto;
  display: flex;
}
.stickybanner__title {
  font-weight: 900;
  font-size: 1rem;
  text-transform: uppercase;
  font-family: "NespressoLucas", "Trebuchet MS", "Lucida Grande",
    "Lucida Sans Unicode", "Lucida Sans", Tahoma, sans-serif;
  color: #000;
  letter-spacing: 3px;
  line-height: 1.5rem;
}
.stickybanner__ribbon_tagline,
.stickybanner__ribbon_tagline_see_all {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 0.5rem;
  // padding-bottom: 6px;
  color: #8f7247;
  font-family: "NespressoLucas", "Trebuchet MS", "Lucida Grande",
    "Lucida Sans Unicode", "Lucida Sans", Tahoma, sans-serif;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1.5rem;
  letter-spacing: 1px;
}

// CSS icons from https://css.gg/

.gg-math-plus {
  margin-top: -2px;
  position: relative;
  transform: scale(var(--ggs, 1));
  width: 16px;
  height: 2px;
  &,
  &:after {
    display: block;
    box-sizing: border-box;
    background: currentColor;
    border-radius: 10px;
  }
  &:after {
    content: "";
    position: absolute;
    width: 2px;
    height: 16px;
    top: -7px;
    left: 7px;
  }
}

.gg-chevron-right {
  box-sizing: border-box;
  position: relative;
  display: block;
  transform: scale(var(--ggs, 1));
  width: 18px;
  height: 18px;
  border: 2px solid transparent;
  border-radius: 100px;
  &:after {
    content: "";
    display: block;
    box-sizing: border-box;
    position: absolute;
    width: 8px;
    height: 8px;
    border-bottom: 2px solid;
    border-right: 2px solid;
    transform: rotate(-45deg);
    right: 4px;
    top: 3px;
  }
}
.stickybanner__ribbon_description {
  color: #000;
  font-size: 1rem;
  font-weight: 300;
  letter-spacing: 1px;
  line-height: 1.5rem;
  text-align: center;
  margin: 0 auto;
  width: 50%;
}
.stickybanner__content_wrapper {
  width: 50%;
  max-width: 615px;
  margin-top: 1rem;
  border-spacing: 0;
  padding: 0.5rem;
  vertical-align: middle;
  text-align: center;
  &:first-child {
    border-right: 1px solid #e6e6e6;
  }
  img {
    width: 45%;
  }
}
.stickybanner__content_question {
  font-family: "NespressoLucas", "Trebuchet MS", "Lucida Grande",
    "Lucida Sans Unicode", "Lucida Sans", Tahoma, sans-serif;
  font-weight: 700;
  font-size: 1rem;
  letter-spacing: 1px;
  line-height: 1.5rem;
  text-align: center;
}
.stickybanner__content_answer {
  font-family: "NespressoLucas", "Trebuchet MS", "Lucida Grande",
    "Lucida Sans Unicode", "Lucida Sans", Tahoma, sans-serif;
  font-weight: 400;
  font-size: 0.875rem;
  letter-spacing: 1px;
  line-height: 1.3125rem;
  text-align: center;
}
.stickybanner__content_rules {
  width: 100%;
  display: flex;
  margin: auto;
  max-width: 996px;
  padding-bottom: 1rem;
}

div.stickybanner__content > div:nth-child(3) {
  padding-left: 32px;
  border-left: solid #000 1px;
}
.stickybanner__content_title {
  font-weight: 700;
  font-size: 15px;
}
.stickybanner__content_button {
  font-size: 14px;
  border-radius: 3px;
  background-color: #3d8705;
  -webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.3);
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.3);
  display: inline-block;
  padding: 0.8em 2em;
  line-height: 1.2em;
  cursor: pointer;
  min-width: 200px;
  margin-top: 16px;
  color: #fff;
  font-weight: 700;
  letter-spacing: 1px;
}
.stickybanner__content_button_contact {
  font-size: 14px;
  border-radius: 3px;
  background-color: #8f7247;
  -webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.3);
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.3);
  display: inline-block;
  padding: 0.8em 2em;
  line-height: 1.2em;
  cursor: pointer;
  min-width: 200px;
  margin-top: 16px;
  color: #fff;
  font-weight: 700;
  letter-spacing: 1px;
  position: relative;
  // .gg-math-plus {
