  //   right: 12px;
  //   top: calc(50% + 1px);
  //   width: 12px;
  //   &:after {
  //     height: 12px;
  //     top: -5px;
  //     left: 5px;
  //   }
  // }
}
.stickybanner__content_button:hover {
  -webkit-box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.3);
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.3);
}
.stickybanner__content_button div > span {
  letter-spacing: 1px;
  text-align: center;
  color: #fff;
  display: block;
}
