@media screen and (max-width: 768px) {
  .stickybanner__container {
    max-height: 100%;
    z-index: 99998;
  }
  .scrollable {
    overflow-y: scroll;
  }
  .stickybanner__content_wrapper img {
    width: 40%;
  }
  .opened {
    .stickybanner__ribbon_content {
      padding-top: 72px;
    }
    .stickybanner__ribbon .stickybanner__plus {
      margin-top: 1rem;
    }
    .stickybanner__ribbon_content_text {
      padding-top: 0;
    }
  }
  .stickybanner__ribbon_content {
    max-width: 80%;
    min-height: 112px;
  }
  .stickybanner__ribbon_content_left,
  .stickybanner__ribbon_content_right {
    width: 7.5%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .stickybanner__ribbon_content_left img {
    width: 140px;
    margin-right: 70px;
  }
  .stickybanner__ribbon_content_right img {
    width: 140px;
    margin-left: 70px;
  }
  .stickybanner__ribbon_content_text {
    width: 65%;
    padding-top: 1rem;
  }
  .stickybanner__ribbon_tagline {
    padding-top: 0;
  }
  .stickybanner__ribbon_description {
    width: 100%;
  }
  .stickybanner__ribbon_tagline_see_all {
    padding-top: 1rem;
  }
  .stickybanner__ribbon .stickybanner__plus {
    transform: translateX(-50%);
    .gg-math-plus {
      transform: rotate(45deg);
    }
    &.overflow {
      transform: translateX(-50%);
      .gg-math-plus {
        transform: rotate(0deg);
      }
    }
  }
  .stickybanner__content {
    padding-left: 4%;
    padding-right: 4%;
  }
  .stickybanner__title {
    padding-top: 0.5rem;
  }
  .stickybanner__content_button {
    font-size: inherit;
    padding: 0.8em 1em;
  }
  .stickybanner__content_button,
  .stickybanner__content_button_contact {
    width: 80%;
    max-width: 280px;
  }
  .stickybanner__content_rules {
    display: block;
    padding-bottom: 0;
  }
  .stickybanner__content_wrapper {
    width: 100%;
    padding: 2rem 0.5rem;
    margin: 0 auto;
    img {
      width: 100%;
      max-width: 256px;
    }
    &:first-child {
      border-right: none;
      border-bottom: 1px solid #e6e6e6;
      padding-top: 0;
    }
  }
}
